import './Score.css'
import { Card, Badge } from '@material-ui/core'


const Score = (props) => {
    return(
        <Card className="score">

            
                <Badge badgeContent={props.score} color='primary'></Badge> 
                
            <br/>
            
            <Badge badgeContent={12} color='secondary'></Badge> 

        </Card>
    )
} 
export default Score